/* CestaWeb - Tailwind Fallback Completo */
* { box-sizing: border-box; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
  max-width: 768px;
  /*padding: 0.8rem 0;*/
}

/* Tailwind bg- colors */
.bg-white { background-color: #ffffff; }
.bg-blue-50 { background-color: #eff6ff; }
.bg-blue-100 { background-color: #dbeafe; }
.bg-blue-500 { background-color: #3b82f6; }
.bg-green-500 { background-color: #10b981; }
.bg-red-500 { background-color: #ef4444; }
.bg-gray-50 { background-color: #f9fafb; }
.bg-indigo-50 { background-color: #eef2ff; }
.bg-purple-50 { background-color: #faf5ff; }
.bg-gradient-to-r { background: linear-gradient(to right, var(--from), var(--to)); }

/* Tailwind text- colors */
.text-blue-500 { color: #3b82f6; }
.text-green-500 { color: #10b981; }
.text-red-500 { color: #ef4444; }
.text-gray-500 { color: #6b7280; }
.text-gray-800 { color: #1f2937; }

/* Tailwind p- padding */
.p-2 { padding: 0.15rem; }
.p-3 { padding: 0.25rem; }
.p-4 { padding: 0.35rem; }
.p-6 { padding: 0.5rem; }
.p-8 { padding: 1rem; }
.px-2 { padding-left: 0.5rem; padding-right: 0.5rem; }
.px-4 { padding-left: 1rem; padding-right: 1rem; }
.px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
.px-8 { padding-left: 2rem; padding-right: 2rem; }
.py-2 { padding-top: 0.5rem; padding-bottom: 0.5rem; }
.py-4 { padding-top: 1rem; padding-bottom: 1rem; }

/* Tailwind m- margin */
.m-4 { margin: 1rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-6 { margin-bottom: 1.5rem; }
.mb-8 { margin-bottom: 2rem; }
.mb-10 { margin-bottom: 2.5rem; }
.mt-4 { margin-top: 1rem; }
.mt-6 { margin-top: 1.5rem; }

/* Tailwind border */
.border { border-width: 1px; border-style: solid; border-color: #d1d5db; }
.border-2 { border-width: 2px; }
.border-gray-200 { border-color: #e5e7eb; }
.border-blue-200 { border-color: #bfdbfe; }
.border-l-4 { border-left-width: 4px; }

/* Tailwind rounded */
.rounded { border-radius: 0.375rem; }
.rounded-lg { border-radius: 0.5rem; }
.rounded-xl { border-radius: 0.75rem; }
.rounded-2xl { border-radius: 1rem; }
.rounded-full { border-radius: 9999px; }

/* Tailwind shadow */
.shadow-md { box-shadow: 0 4px 6px -1px rgba(0, 0,0, 0.1); }
.shadow-lg { box-shadow: 0 10px 15px -3px rgba(0, 0,0, 0.1); }
.shadow-xl { box-shadow: 0 20px 25px -5px rgba(0, 0,0, 0.1); }
.shadow-2xl { box-shadow: 0 25px 50px -12px rgba(0, 0,0, 0.25); }
.hover\:shadow-xl:hover { box-shadow: 0 20px 25px -5px rgba(0, 0,0, 0.1); }

/* Tailwind text sizes */
.text-sm { font-size: 0.675rem; }
.text-lg { font-size: 0.9rem; }
.text-xl { font-size: 1rem; }
.text-2xl { font-size: 1.25rem; }
.text-3xl { font-size: 1.5rem; }
.text-5xl { font-size: 2rem; }
.font-bold { font-weight: 700; }
.font-black { font-weight: 900; }

/* Tailwind flex/grid */
.flex { display: flex; }
.grid { display: grid; }
.flex-1 { flex: 1 1 0%; }
.items-center { align-items: center; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }
.gap-2 { gap: 0.5rem; }
.gap-3 { gap: 0.75rem; }

/* Tailwind transitions */
.transition-all { transition-property: all; transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1); transition-duration: 150ms; }
.hover\:bg-blue-600:hover { background-color: #2563eb; }
.hover\:text-blue-700:hover { color: #1d4ed8; }

/* Container */
.container { /*max-width: 1200px;*/ margin: 0 auto; padding: 0 1rem; }
.mx-auto { margin-left: auto; margin-right: auto; }
.max-w-6xl { max-width: 72rem; }
.min-h-screen { min-height: 100vh; }

/* Glassmorphism */
.glass { 
  background: rgba(255,255,255,0.25); 
  backdrop-filter: blur(10px); 
  border: 1px solid rgba(255,255,255,0.18); 
}

/* Responsive */
@media (max-width: 768px) {
  .container { padding: 0 0.5rem; }
}
button { min-width: 32px; 
          font-size: 1rem; 
          border: 1px solid gray }

.w-full {
  width:100%;
}

.two-col {
  columns: 2;          /* or: column-count: 2; */
  column-fill: balance;   /* fill left column first, then right */
  column-gap: 2rem;    /* space between columns */
  list-style: none;
  padding: 0;
  margin: 0;
}
.two-col li {
  break-inside: avoid; /* keep items from splitting between columns */
}